.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;
  background-position: center;
  transition: transform $effect-duration;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../icons/Icon-Phone-call.svg);

    @include on-desktop {
      position: relative;
      display: inline-block;
    }

    @include on-desktop {
      &:hover::after {
        content: '+1 234 555-55-55';
        position: absolute;
        left: -140px;
        top: 7px;
        color: #1B2129;
        font-family: Inter, sans-serif;
        font-size: 12px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
      }
    }
  }

  &--menu {
    background-image: url(../icons/Icon-Burger-menu.svg);

    &:hover {
      background-image: url(../icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../icons/Icon-Close.png);
  }
}


