.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #F9F9F9;
  font-weight: 700;
  overflow: auto;

  &__top {
    margin-bottom: 32px;
    padding-inline: 20px;

    @include on-tablet {
      padding-inline: 72px;
    }

    @include on-desktop {
      padding-inline: 120px;
    }

    position: sticky;
    top: 0;
    z-index: 1;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__content {
    padding-inline: 20px;

    @include on-tablet {
      padding-inline: 72px;
    }

    @include on-desktop {
      padding-inline: 120px;
    }
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    letter-spacing: 2px;
    line-height: 22.4px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;

    color: inherit;
    text-decoration: none;
    font-style: 16px;
    letter-spacing: 2px;
    line-height: 22.4px;
  }
}


